import React, { Component } from 'react';
import { Link } from 'react-router-dom';


const API_KEY = 'AIzaSyB2XywM2HNdu6S795vRPSC_WS5VgRaEbGg';
const url = `https://www.googleapis.com/youtube/v3/search?part=snippet&maxResults=12&q=wrestling&type=video&key=${API_KEY}`

//https://www.googleapis.com/youtube/v3/search?part=snippet&maxResults=10&q=wrestling&type=video&key=[YOUR_API_KEY]

class Agree extends Component {
    constructor(props){
        super(props);

        this.state ={
            query: '',
            resulty: [],
            loading: false
        }
    }
    handleOnInputChange = (event) =>{
        const query = event.target.value;
        this.setState( {query: query, loading: true} );

        fetch(url)
            .then( (response) =>response.json() )
            .then((responseJson) => {
               // console.log(responseJson);
               const resulty = responseJson.items.map(obj => "https://www.youtube.com/embed/"+obj.id.videoId);
               this.setState({resulty});
               
            })
            .catch((error) => {
                console.error(error);
            });
    };
    render() { 
        const {query} = this.state;
        //console.log(url);
        console.log(this.state.resulty);
        return(
            <div>
                <h2>K-Entertianmet Search App</h2>
                    <label className='search-lable' htmlFor='search-input'>
                        <input type='text'
                            name='query'
                            value={query}
                            id='search-input'
                            placeholder='i.e wrestling search...'
                            onChange={this.handleOnInputChange}
                        />
                        <i class="fa fa-search search-icon" aria-hidden="true" />
                        <h4>Loading...</h4>
                    </label>
                    



                        {
                            this.state.resulty.map((link, i) => {
                                console.log(link);
                                var frame = <div className='youtube'><iframe key={i} width="400" height="300" src={link} frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe></div>
                                return frame;
                            })
                        }
                            {this.frame}
                <div className='l2'>
                    <Link to='/'>
                        <button className='home'><li>Home</li></button>
                    </Link>
                </div>
            </div>
        );
    }
}
 
export default Agree;