import React from 'react';

function Card (props) {
    return(
        <div style={style.card}>
            <img style={style.img} src={props.card_number} />
        </div>
    );
}

const style ={
    card:{
        width: '350px',
        height: '200px',
        // backgroundColor: 'blue',
        // border: '1px solid black',
        fontsizing: '2.5em',
        color: 'white'
    },
    img: {
        width: '100%',
        height: '100%'
    }
}
export default Card;